<template>
    <div id="app">
      <h1>App.vue</h1>
      {{ message }}
      <br />
      <!--
      <img src="./assets/TTCoin.png"/>
      <router-view/>
      -->
      <div>
        <hello-world></hello-world>
      </div>
      <div>
        <new-grid></new-grid>
      </div>
    </div>
</template>

<script>
import NewGrid from './components/NewGrid.vue'
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: { HelloWorld, NewGrid },
  data () {
    return {
      message: 'Hello from App.vue'
    }
  }
}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



